<template>
  <div>
    <h1>
      {{sections.portfolio.heading}}
    </h1>
    <div class="row">
      <div class="col-lg-4 col-md-6 col-sm-6 portfolio-item" v-for="item in portfolio" :key="item.title">
        <div>
          <img class="image" :alt="item.title" :src="'static/portfolio/' + item.name + '.png'" />
          <div class="meta">
            <p class="description">
              <span class="title">{{item.title}}</span> <br/> {{item.description}}
            </p>
            <div class="tags">
              <span class="label-custom light" v-for="skill in item.skills" :key="skill" v-html="skill"></span>
            </div>
            <div class="links">
              <a class="url" v-if="item.url" :href="item.url">Open</a>
              <a class="source" v-if="item.source" :href="item.source">Source Code</a>
              <div class="clearfix"></div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex'

  export default {
    components: {},
    computed: {
      ...mapGetters(['sections', 'portfolio'])
    }
  }
</script>

<style>
  .portfolio-item {
    padding-bottom: 30px;
  }

  .portfolio-item>div {
    border: 1px solid #ecf0f1;
    position: relative;
    height: 220px;
    overflow: hidden;
  }

  .portfolio-item .meta {
    text-align: center;
    color: #fff;
    position: absolute;
    left: 0;
    bottom: -100%;
    width: 100%;
    padding: 10px;
    background-color: #2c3e50;
    transition: all 0.25s ease-out;
  }

  .portfolio-item .title {
    color: #18bc9c;
    font-weight: bold;
  }

  .portfolio-item .image {
    position: relative;
    height: auto;
    width: 100%;
    left: 0;
    right: 0;
    transition: all 0.25s ease-out;
  }

  .portfolio-item .links {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
  }

  .portfolio-item .tags {
    position: absolute;
    left: 0;
    bottom: 25px;
    width: 100%;
  }

  .portfolio-item .links a {
    display: inline-block;
    width: 48%;
    text-align: right;
    padding: 5px 10px;
  }

  .portfolio-item .links a.url {
    float: left;
    text-align: left;
  }

  .portfolio-item .links a.source {
    float: right;
  }

  .portfolio-item:hover .image {
    -webkit-filter: grayscale(1);
    filter: grayscale(1);
  }

  .portfolio-item:hover .meta {
    bottom: 0;
    height: 100%;
  }

  .label-custom.light {
    border-color: rgba(255, 255, 255, 0.25);
    color: #ecf0f1;
  }
</style>



