import Vue from 'vue'

var mixins = {
  methods: {
    goto (section) {
      let sectionId = 'section-' + section.replace(/ /g, '').toLowerCase()
      document.getElementById(sectionId).scrollIntoView({
        behavior: 'smooth'
      })

      if (section.toLowerCase() === 'contact') {
        window.setTimeout(() => {
          document.getElementById('txt_email').focus()
        }, 500)
      }
    }
  }
}

Vue.mixin(mixins)



