<template>
  <div>
    <h1>
      {{sections.skills.heading}}
    </h1>
    <div class="row certifications">
      <div class="col-md-4 col-sm-6">
        <a href="https://www.freecodecamp.org/mubaidr/front-end-certification" title="Verify this certification" target="_blank"><img src="/static/docs/front-end.jpg" /></a>
      </div>
      <div class="col-md-4 col-sm-6">
        <a href="https://www.freecodecamp.org/mubaidr/back-end-certification" title="Verify this certification" target="_blank"><img src="/static/docs/back-end.jpg" /></a>
      </div>
    </div>
    <div>
      <span class="label-custom" v-for="skill in skills" :key="skill.title" v-html="skill.title"></span>
    </div>
  </div>
</template>
<script>
  import { mapGetters } from 'vuex'

  export default {
    data () {
      return {}
    },
    computed: {
      ...mapGetters(['sections', 'skills'])
    }
  }
</script>
<style>
  .label-custom {
    display: inline-block;
    margin: 0 5px 5px 0;
    font-size: 0.75em;
    font-weight: lighter;
    padding: 0.25em 0.5em;
    color: #222;
    border: 1px solid #ecf0f1;
  }

  .certifications img {
    width: 100%;
    height: auto;
    margin-bottom: 21px;
  }
</style>



