<template>
  <div>
    <section-header></section-header>
    <div class="container body">
      <section-about :id="getSectionId(sections.about.heading)"></section-about>
      <section-skills :id="getSectionId(sections.skills.heading)"></section-skills>
      <section-portfolio :id="getSectionId(sections.portfolio.heading)"></section-portfolio>
      <section-contact :id="getSectionId(sections.contact.heading)"></section-contact>
    </div>
    <section-footer></section-footer>

    <transition name="slide-left" appear mode="out-in">
      <a class="btn btn-primary btn-scroll-to-top" @click.stop.prevent="gotoTop" v-show="scrollButtonVisible">
        <i class="material-icons">arrow_upward</i>
      </a>
    </transition>
  </div>
</template>

<script>
  import debounce from 'lodash.debounce'
  import { mapGetters } from 'vuex'

  import Header from 'components/layout/Header'
  import Footer from 'components/layout/Footer'
  import about from 'components/sections/About'
  import portfolio from 'components/sections/Portfolio'
  import skills from 'components/sections/Skills'
  import contact from 'components/sections/Contact'

  export default {
    name: 'app',
    components: {
      'section-header': Header,
      'section-footer': Footer,
      'section-about': about,
      'section-skills': skills,
      'section-portfolio': portfolio,
      'section-contact': contact
    },
    data () {
      return {
        scrollButtonVisible: false
      }
    },
    computed: {
      ...mapGetters(['sections', 'info'])
    },
    methods: {
      gotoTop () {
        window.scroll({
          top: 0,
          left: 0,
          behavior: 'smooth'
        })
      },
      getSectionId (heading) {
        return 'section-' + heading.toLowerCase().replace(/ /g, '')
      }
    },
    created () {
      var _self = this
      document.body.onscroll = debounce(() => {
        let top = (document.documentElement && document.documentElement.scrollTop) || document.body.scrollTop
        _self.scrollButtonVisible = top > 135
      }, 250, {
          leading: false,
          trailing: true
        })
    }
    /*,
    mounted () {
      this.$nextTick(() => {
        window.setTimeout(() => {
          document.dispatchEvent(new Event('custom-post-render-event'))
        }, 5000)
      })
    }*/
  }
</script>

<style>
  .container.body {
    min-height: calc(100vh - 135px)
  }

  .btn-scroll-to-top {
    cursor: pointer;
    position: fixed;
    bottom: 159px;
    right: 24px;
    padding: 10px;
  }
</style>



