import './assets/css/bootstrap.css'
import './assets/css/animations.css'
import './assets/css/site.css'

import './pollyfills'
import './mixins'

import Vue from 'vue'
import store from './store'
import App from './App'

/* eslint-disable no-new */
new Vue({
  el: '#app',
  store: store,
  template: '<App/>',
  components: {
    App
  }
})



